<template>
	<div id="creditshop-order">
		<public-header title="积分兑换"></public-header>
		<div class="content">
			<div class="wrap-search wrap-search-input">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.status == 0}" @click="onToggleStatus(0)">全部</div>
					<div class="tab-item" :class="{active: filter.items.status == 2}" @click="onToggleStatus(2)">已处理</div>
					<div class="tab-item" :class="{active: filter.items.status == 1}" @click="onToggleStatus(1)">未处理</div>
				</div>
				<van-search placeholder="请输入用户昵称/UID" v-model="filter.items.keyword" />
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="order-list"
			>
				<div class="order-item" v-for="(item, index) in records.data" :key="item.id">
					<div class="order-title flex-lr">
						<div class="title flex">
							<div class="title-img">
								<img :src="item.avatar" alt="">
							</div>
							<div class="title-name">{{item.nickname}}</div>
							<div class="font-14 c-gray padding-10-r" v-if="item.goods_type == 'goods'">{{item.title}} <div class="gift-type itag itag-danger">商品</div></div>
							<div class="font-14 c-gray padding-10-r" v-else-if="item.goods_type == 'redpacket'">{{item.title}} <div class="gift-type itag itag-danger">红包</div></div>
							<div class="font-14 c-gray padding-10-r" v-else-if="item.goods_type == 'credit2'">{{item.title}} <div class="gift-type itag itag-danger">余额</div></div>
						</div>
						<div class="itag itag-danger" v-if="item.status == 1">未处理</div>
						<div class="itag itag-primary" v-else>已处理</div>
					</div>
					<div class="order-detail border-1px-t">
						<div class="order-info flex">
							<div>兑换码：</div>
							<div class="c-danger">{{item.code}}</div>
						</div>
						<div class="order-info flex">
							<div>支付方式：</div>
							<div>{{item.use_credit1}}积分 <template v-if="item.use_credit2 > 0">+{{item.use_credit2}}元</template></div>
						</div>
						<template v-if="item.goods_type == 'goods'">
							<div class="order-info flex">
								<div>收件人：</div>
								<div>{{item.username}}</div>
							</div>
							<div class="order-info flex">
								<div>手机号：</div>
								<div class="c-info" @click="util.jsUrl('tel:' + item.mobile)">{{item.mobile}} <i class="icon icon-phone2"></i></div>
							</div>
						</template>
						<div class="order-info flex">
							<div>兑换时间：</div>
							<div>{{item.addtime_cn}}</div>
						</div>
					</div>
					<div class="border-1px-t btn-group" v-if="item.goods_type == 'goods' && item.is_pay == 1 && item.status == 1">
						<div class="btn-item bg-default" @click="onChangeHandle(item, index)">设为已处理</div>
					</div>
				</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>没有符合条件的数据!</p>
			</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				isRefresh: false,
				showPreLoading: true,
				filter: {
					items: {
						status: '0'
					}
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/creditshop/order/order'
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onChangeHandle(item, index){
				var that = this;
				that.util.jspost({
					url: 'plateform/creditshop/order/handle',
					data: {
						id: item.id
					},
					confirm: '确定更改订单状态为已处理吗？',
					success: function(){
						that.records.data[index].status = 2;
					}
				});
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
			}
		},
		mounted(){
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			}
		}
	}
</script>

<style>
	#creditshop-order .content .order-item{
		margin-bottom: 10px;
		background-color: #fff;
	}
	#creditshop-order .content .order-item .order-title{
		padding: 10px;
	}
	#creditshop-order .content .order-item .order-title .title-img{
		width: 35px;
		height: 35px;
	}
	#creditshop-order .content .order-item .order-title .title-img img{
		width: 100%;
		height: 100%;
		border-radius: 100%;
	}
	#creditshop-order .content .order-item .order-title .title-name{
		font-size: 16px;
		font-weight: bold;
		padding: 0 10px;
	}
	#creditshop-order .content .order-item .order-title .gift-type{
		font-size: 12px;
		padding: 2px 4px;
	}
	#creditshop-order .content .order-item .order-detail{
		padding: 10px;
	}
	#creditshop-order .content .order-item .order-detail .order-info{
		font-size: 14px;
		color: #7a7a7a;
		padding-bottom: 8px;
	}
	#creditshop-order .content .order-item .order-detail .order-info:last-child{
		padding-bottom: 0px;
	}
	#creditshop-order .content .order-item .order-detail .order-info .icon-phone2{
		padding-left: 5px;
		font-size: 16px;
	}
	#creditshop-order .content .order-item .btn-group{
		display: flex;
		flex-wrap: nowrap;
		justify-content: flex-end;
		align-items: center;
		width: 100%;
		padding: 10px;
	}
	#creditshop-order .content .order-item .btn-group .btn-item{
		min-width: 60px;
		margin-left: 10px;
		text-align: center;
		padding: 10px 8px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		font-size: 14px;
	}
</style>


